<template>
    <li class="cart__item product">
        <div class="product__pic">
            <img :src="item.product.image" width="120" height="120" :alt="item.product.title">
        </div>
        <h3 class="product__title">
            {{ item.product.title }}
        </h3>
        <span class="product__code">
            Артикул: {{ item.product.id }}
        </span>

        <CounterItem v-model="amount"/>

        <b class="product__price">
            {{ (item.amount * item.product.price) | numberFormat }} ₽
        </b>

        <div v-show="failedDeleteProductCart">Что то пошло не так...</div>

        <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины"
            @click.prevent="deleteProduct(item.productId)"
        >
            <svg width="20" height="20" fill="currentColor">
                <use xlink:href="#icon-close"></use>
            </svg>
        </button>
    </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import CounterItem from '@/components/CounterItem.vue';
import { mapActions } from 'vuex';

export default {
    props: ['item'],
    components: {
        CounterItem
    },  
    filters: {
        numberFormat
    },
    data() {
        return {
            failedDeleteProductCart: false
        }
    },
    methods: {
        ...mapActions(['deleteProductFromCart']),
        deleteProduct(id) {
            this.failedDeleteProductCart = false;
            this.deleteProductFromCart(id)
            .catch((error) => {
                this.failedDeleteProductCart = true;
                console.log(error)
            })
        }
    },
    computed: {
        amount: {
            get() {
                return this.item.amount;
            },
            set(value) {
                this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: value})
            }
        }
    }
}
</script>