<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ $store.state.cartProductsData.length | normalizeWords }}
      </span>
    </div>

    <div style="display: flex; justify-content: center;" v-if="$store.state.loadCartProducts">
      <img src="../../public/img/load.gif">
    </div>
    <div v-if="$store.state.failedLoadCartProducts">Что то пошло не так...</div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">

            <CartItem v-for="item in products" :key="item.productId" :item="item" />

          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link tag="button" :to="{ name: 'order' }" class="cart__button button button--primery" type="submit"
            v-show="this.$store.state.cartProductsData.length">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import CartItem from '@/components/CartItem.vue';
import { normalizeWords } from '@/helpers/normalizeWords';

export default {
  components: {
    CartItem
  },
  filters: {
    numberFormat,
    normalizeWords
  },
  computed: {
    ...mapGetters({
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice'
    })
  },
}
</script>