import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        cartProducts: [],
        cartProductsData: [],
        allProductsQuantity: [],
        userAccessKey: null,
        loadCartProducts: false,
        failedLoadCartProducts: false,
        orderInfo: null,
        loadOrderInfo: false,
        failedLoadOrderInfo: false,
    },
    mutations: {
        updateLoadOrderInfo(state, value) {
            state.loadOrderInfo = value;
        },
        updateFailedLoadOrderInfo(state, value) {
            state.failedLoadOrderInfo = value;
        },
        updateOrderInfo(state, orderInfo) {
            state.orderInfo = orderInfo;
        },
        resetCart(state) {
            state.cartProducts = [];
            state.cartProductsData = [];
        },
        updateAllProductsQuantity(state, items) {
            state.allProductsQuantity = items;
        },
        updateCartProductAmount(state, { productId, amount }) {
            const item = state.cartProducts.find(item => item.productId === productId);

            if (item) {
                item.amount = amount;
            }
        },
        deleteCartProduct(state, productId) {
            state.cartProducts = state.cartProducts.filter(item => item.productId !== productId);
        },
        updateUserAccessKey(state, accessKey) {
            state.userAccessKey = accessKey;
        },
        updateCartProductsData(state, items) {
            state.cartProductsData = items;
        },
        syncCartProducts(state) {
            state.cartProducts = state.cartProductsData.map(item => {
                return {
                    productId: item.product.id,
                    amount: item.quantity
                }
            })
        },
        updateLoadCartProducts(state, value) {
            state.loadCartProducts = value;
        },
        updateFailedLoadCartProducts(state, value) {
            state.failedLoadCartProducts = value;
        },
    },
    getters: {
        cartDetailProducts(state) {
            return state.cartProducts.map(item => {
                const product = state.cartProductsData.find(p => p.product.id === item.productId).product
                return {
                    ...item,
                    product: {
                        ...product,
                        image: product.image.file.url
                    }
                }
            })
        },
        cartTotalPrice(state, getters) {
            return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0)
        }
    },
    actions: {
        loadOrderInfo(context, orderId) {
            context.commit('updateLoadOrderInfo', true);
            context.commit('updateFailedLoadOrderInfo', false);
            return axios
                .get(`${API_BASE_URL}/api/orders/${orderId}`, {
                    params: {
                        userAccessKey: context.state.userAccessKey
                    }
                })
                .then(response =>
                    context.commit('updateOrderInfo', response.data))
                .catch((error) => {
                    context.commit('updateFailedLoadOrderInfo', true);
                    console.log(error);
                })
                .then(() => context.commit('updateLoadOrderInfo', false));
        },
        loadAllProductsQuantity(context) {
            return axios
                .get(`${API_BASE_URL}/api/products`)
                .then(response => context.commit('updateAllProductsQuantity', response.data.items))
                .catch((error) => console.log(error))
        },
        loadCartProducts(context) {
            context.commit('updateLoadCartProducts', true);
            context.commit('updateFailedLoadCartProducts', false);
            return (new Promise(resolve => setTimeout(resolve, 1000)))
                .then(() => {
                    return axios
                        .get(`${API_BASE_URL}/api/baskets`, {
                            params: {
                                userAccessKey: context.state.userAccessKey
                            }
                        })
                        .then(response => {
                            if (!context.state.userAccessKey) {
                                localStorage.setItem('userAccessKey', response.data.user.accessKey);
                                context.commit('updateUserAccessKey', response.data.user.accessKey);
                            }
                            context.commit('updateCartProductsData', response.data.items);
                            context.commit('syncCartProducts');
                        })
                        .catch((error) => {
                            context.commit('updateFailedLoadCartProducts', true);
                            console.log(error);
                        })
                        .then(() => context.commit('updateLoadCartProducts', false));
                })

        },
        addProductToCart(context, { productId, amount }) {
            return (new Promise(resolve => setTimeout(resolve, 1000)))
                .then(() => {
                    return axios
                        .post(`${API_BASE_URL}/api/baskets/products`, {
                            productId,
                            quantity: amount
                        }, {
                            params: {
                                userAccessKey: context.state.userAccessKey
                            }
                        })
                        .then(response => {
                            context.commit('updateCartProductsData', response.data.items);
                            context.commit('syncCartProducts');
                        })
                })
        },
        deleteProductFromCart(context, productId) {
            return axios
                .delete(`${API_BASE_URL}/api/baskets/products`, {
                    params: {
                        userAccessKey: context.state.userAccessKey
                    },
                    data: { productId }
                })
                .then(response => {
                    context.commit('deleteCartProduct', productId);
                    context.commit('updateCartProductsData', response.data.items);
                    context.commit('syncCartProducts');
                })
        },
        updateCartProductAmount(context, { productId, amount }) {
            context.commit('updateCartProductAmount', { productId, amount });
            if (amount < 1) {
                return
            }
            return axios
                .post(`${API_BASE_URL}/api/baskets/products`, {
                    productId: productId,
                    quantity: amount
                }, {
                    params: {
                        userAccessKey: context.state.userAccessKey
                    }
                })
                .then(response => {
                    context.commit('updateCartProductsData', response.data.items);
                })
                .catch((error) => {
                    context.commit('syncCartProducts');
                    console.log(error);
                })
        }
    }
});