<template>
    <div class="product__counter form__counter">
        <button type="button" aria-label="Убрать один товар" :disabled="value <= 1" @click="dicrement">
            <svg width="10" height="10" fill="currentColor">
                <use xlink:href="#icon-minus"></use>
            </svg>
        </button>
        <input type="number" v-model.number="inputValue" name="count">
        <button type="button" aria-label="Добавить один товар" @click="increment">
            <svg width="10" height="10" fill="currentColor">
                <use xlink:href="#icon-plus"></use>
            </svg>
        </button>
    </div>
</template>

<script>

export default {
    model: {
        prop: 'value',
        event: 'change'
    },
    props: ['value'],
    computed: {
        inputValue: {
            get() {
                return this.value;
            },
            set(value) {
                this.$emit('change', value);
            }
        },
    },
    methods: {
        dicrement() {
            if (this.value > 1) {
                this.$emit('change', this.value - 1);
            }
        },
        increment() {
            this.$emit('change', this.value + 1);
        }
    }
}
</script>