<template>
    <li class="colors__item">
        <label class="colors__label">
            <input class="colors__radio sr-only" type="radio" :value="color.code" :name="'productColor'"
                v-model="currentColor">
            <span class="colors__value" :style="{ backgroundColor: color.code }">
            </span>
        </label>
    </li>
</template>

<script>
export default {
    props: [
        'color'
    ],
    data() {
        return {
            currentColor: ''
        }
    },
    watch: {
        currentColor() {
            this.$emit('currentColor', this.color.id);
            this.currentColor = '';
        }
    }
}
</script>